import React from 'react';
import './SideNav.css';
import ExpandedSideNav from './ExpandedSideNav';
import HomeIcon from '@material-ui/icons/Home';
import WhatshotIcon from '@material-ui/icons/Whatshot';
import SubscriptionsIcon from '@material-ui/icons/Subscriptions';
import VideoLibraryIcon from '@material-ui/icons/VideoLibrary';

const SideNav = ( {navOpen} ) => {
    return (
        <nav className="side-navbar-container">
            <ExpandedSideNav 
                navOpen={navOpen}
            />
            <ul className="side-navbar-list">
                <div className="side-nav-item">
                    <a href='/'>
                        <HomeIcon className="nav-icons"/>
                        <li>Home</li>
                    </a> 
                </div>
                <div className="side-nav-item">
                    <a href='/'>
                        <WhatshotIcon className="nav-icons"/>
                        <li>Trending</li>
                    </a>
                </div>
                <div className="side-nav-item">
                    <a href='/'>
                        <SubscriptionsIcon className="nav-icons"/>
                        <li>Subscriptions</li>
                    </a>
                </div>
                <div className="side-nav-item">
                    <a href='/'>
                        <VideoLibraryIcon className="nav-icons"/>
                        <li>Library</li>
                    </a>
                </div>
            </ul>
        </nav>    
    )
}

export default SideNav;