import React from 'react';
import './ProfileMenu.css';
import AccountBoxIcon from '@material-ui/icons/AccountBox';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import Brightness4Icon from '@material-ui/icons/Brightness4';
import TranslateIcon from '@material-ui/icons/Translate';
import LanguageIcon from '@material-ui/icons/Language';
import SettingsIcon from '@material-ui/icons/Settings';
import HelpIcon from '@material-ui/icons/Help';
import FeedbackIcon from '@material-ui/icons/Feedback';
import KeyboardIcon from '@material-ui/icons/Keyboard';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';

const ProfileMenu = ( {profileOpen, profileImg} ) => {
    return (
        <nav className={`${profileOpen ? 'profile-navbar-container' : 'hidden'}`}>
            <ul className="profile-header">
                <div className="profile-info">
                    <span>
                        <img className="profile-pic" id="profile-header-pic"src={profileImg}></img>
                    </span>
                    <div className="profile-text">
                        <h3>Devon Miles</h3>
                        <h4>Devonmiles13@gmail.com</h4>
                        <div className="manage-google-acc">
                            <a href='/'>Manage your google account</a>
                        </div>
                    </div>
                </div>
            </ul>
            <span className="divider"></span>
            <ul className="main-section">
                <div className="profile-nav-item">
                    <AccountBoxIcon className="profile-nav-icon"/>
                    <li>Your channel</li>
                </div>
                <div className="profile-nav-item">
                    <MonetizationOnIcon className="profile-nav-icon"/>
                    <li>Purchases and memberships</li>
                </div>
                <div className="profile-nav-item">
                    <span class="iconify profile-nav-icon" data-icon="mdi-youtube-studio" data-inline="false"></span>
                    <li>YouTube Studio</li>
                </div>
                <div className="profile-nav-item">
                    <span class="iconify profile-nav-icon" data-icon="mdi:account-box-multiple" data-inline="false"></span>
                    <li>Switch account</li>
                    <KeyboardArrowRightIcon className="expand-arrow"/>
                </div>
                <div className="profile-nav-item">
                    <ExitToAppIcon className="profile-nav-icon"/>
                    <li>Sign out</li>
                </div>
                <span className="divider"></span>
                <div className="profile-nav-item">
                    <Brightness4Icon className="profile-nav-icon"/>
                    <li>Appearance: Dark</li>
                    <KeyboardArrowRightIcon className="expand-arrow"/>
                </div>
                <div className="profile-nav-item">
                    <TranslateIcon className="profile-nav-icon"/>
                    <li>Language: English</li>
                    <KeyboardArrowRightIcon className="expand-arrow"/>
                </div>
                <div className="profile-nav-item">
                    <LanguageIcon className="profile-nav-icon"/>
                    <li>Location: United States</li>
                    <KeyboardArrowRightIcon className="expand-arrow"/>
                </div>
                <div className="profile-nav-item">
                    <SettingsIcon className="profile-nav-icon"/>
                    <li>Settings</li>
                </div>
                <div className="profile-nav-item">
                    <span class="iconify profile-nav-icon" data-icon="mdi:shield-account-outline" data-inline="false"></span>
                    <li>Your data in YouTube</li>
                </div>
                <div className="profile-nav-item">
                    <HelpIcon className="profile-nav-icon"/>
                    <li>Help</li>
                </div>
                <div className="profile-nav-item">
                    <FeedbackIcon className="profile-nav-icon"/>
                    <li>Send Feedback</li>
                </div>
                <div className="profile-nav-item">
                    <KeyboardIcon className="profile-nav-icon"/>
                    <li>Keyboard shortcuts</li>
                </div>
                <span className="divider"></span>
                <div className="profile-nav-item restricted">
                    <li>Restricted Mode: Off</li>
                    <KeyboardArrowRightIcon className="expand-arrow"/>
                </div>
            </ul>
        </nav>
    )
}

export default ProfileMenu;