import React from 'react';
import './TopNav.css';
import Tooltip from '@material-ui/core/Tooltip';
import MicIcon from '@material-ui/icons/Mic';
import VideoCallIcon from '@material-ui/icons/VideoCall';
import AppsIcon from '@material-ui/icons/Apps';
import NotificationsIcon from '@material-ui/icons/Notifications';
import { withStyles } from '@material-ui/core/styles';
import ProfileMenu from './ProfileMenu';
import {useState} from 'react'
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import OndemandVideoIcon from '@material-ui/icons/OndemandVideo';
import WifiTetheringIcon from '@material-ui/icons/WifiTethering';
import YouTubeIcon from '@material-ui/icons/YouTube';
import AlbumIcon from '@material-ui/icons/Album';
import EmojiEmotionsIcon from '@material-ui/icons/EmojiEmotions';
import Avatar from '@material-ui/core/Avatar';

const CustomTooltip = withStyles(() => ({
    tooltip: {
        fontSize: 12,
        padding: 9
    },
}))(Tooltip);

const IconStyles = {
    color: 'white',
    cursor: 'pointer'
}

const TopNav = ( {navOpen, setNavOpen, profileOpen, setProfileOpen, profileImg} ) => {

    const [createOpen, setCreateOpen] = useState(false);
    const [appsOpen, setAppsOpen] = useState(false);

    const handleDrawerToggle = () => {
        if (navOpen === true) {
            setNavOpen(false);
        } else {
            setNavOpen(true);
        }
    };
    const handleCreateToggle = () => {
        if (createOpen === true) {
            setCreateOpen(false);
        } else {
            setCreateOpen(true);
        }
    };
    const handleAppsToggle = () => {
        if (appsOpen === true) {
            setAppsOpen(false);
        } else {
            setAppsOpen(true);
        }
    };
    const handleProfileToggle = () => {
        if (profileOpen === true) {
            setProfileOpen(false);
        } else {
            setProfileOpen(true);
        }
    };
    const handleCreateClickAway = () => {
        setCreateOpen(false);
    }
    const handleAppsClickAway = () => {
        setAppsOpen(false);
    }
    const handleProfileClickAway = () => {
        setProfileOpen(false);
    }
    return (
        <nav className="top-navbar-container">
                <li className="nav-button" onClick={handleDrawerToggle}>
                    <span class="bar"></span>
                    <span class="bar"></span>
                    <span class="bar"></span>
                </li>
            <span className="logo-container">
                <a href="/" className="yt-link">
                    <img className="ytlogo" src='../images/yt_logo_rgb_dark.png'/>
                </a>
            </span>
            <div className="search-container">
                <input className="search-input" type="text" placeholder="Search"/>
                <button className="search-btn"><span class="material-icons" id="search-icon">search</span></button>
                <CustomTooltip title="Search with your voice">
                    <MicIcon style={IconStyles} className="tooltip-btn"/>
                </CustomTooltip>
            </div>
            <div className="create-container">
                <ul className="create-list">
                    <ClickAwayListener onClickAway={handleCreateClickAway}>
                        <div className="create-nav-item">
                            <a>
                                <CustomTooltip title="Create" onClick={handleCreateToggle}>
                                    <VideoCallIcon style={IconStyles} className="tooltip-btn"/>
                                </CustomTooltip>
                            </a> 
                            <ul className={`${createOpen ? "create-mini-nav" : "hidden"}`}>
                                <span className="top-divider"></span>
                                <div className="create-mini-nav-item">
                                    <OndemandVideoIcon className="mini-nav-icon"/>
                                    <li>Upload video</li>
                                </div>
                                <div className="create-mini-nav-item">
                                    <WifiTetheringIcon className="mini-nav-icon"/>
                                    <li>Go live</li>
                                </div>
                                <span className="top-divider"></span>
                            </ul>
                        </div>
                    </ClickAwayListener>
                    <ClickAwayListener onClickAway={handleAppsClickAway}>
                        <div className="create-nav-item">
                            <a>
                                <CustomTooltip title="YouTube Apps" onClick={handleAppsToggle}>
                                    <AppsIcon style={IconStyles} className="tooltip-btn"/>
                                </CustomTooltip>
                            </a> 
                            <ul className={`${appsOpen ? "apps-mini-nav" : "hidden"}`}>
                                <span className="top-divider"></span>
                                <div className="create-mini-nav-item">
                                    <OndemandVideoIcon className="mini-nav-icon"/>
                                    <li>YouTube TV</li>
                                </div>
                                <span className="divider"></span>
                                <div className="create-mini-nav-item">
                                    <AlbumIcon className="mini-nav-icon"/>
                                    <li>YouTube Music</li>
                                </div>
                                <div className="create-mini-nav-item">
                                    <EmojiEmotionsIcon className="mini-nav-icon"/>
                                    <li>YouTube Kids</li>
                                </div>
                                <span className="divider"></span>
                                <div className="create-mini-nav-item">
                                    <YouTubeIcon className="mini-nav-icon"/>
                                    <li>Creator Academy</li>
                                </div>
                                <div className="create-mini-nav-item">
                                    <YouTubeIcon className="mini-nav-icon"/>
                                    <li>YouTube for Artists</li>
                                </div>
                                <span className="top-divider"></span>
                            </ul>
                        </div>
                    </ClickAwayListener>
                    <div className="create-nav-item">
                        <a href='/'>
                            <CustomTooltip title="Notifications">
                                <NotificationsIcon style={IconStyles} className="tooltip-btn"/>
                            </CustomTooltip>
                        </a> 
                    </div>
                    <ClickAwayListener onClickAway={handleProfileClickAway}>
                        <div className="create-nav-item">
                        <Avatar alt="Profile Imange" className="profile-pic" onClick={handleProfileToggle} src={profileImg}/>
                    </div>
                    </ClickAwayListener>
                    <ProfileMenu 
                        profileOpen={profileOpen}
                        profileImg={profileImg}
                    />
                </ul>
            </div>
        </nav>
    )
}

export default TopNav;