import React from 'react';
import './Main.css';
import Video from './Video';
import Tabs from '@material-ui/core/Tabs';
import Chip from '@material-ui/core/Chip';
import { withStyles } from '@material-ui/core/styles';
import { useEffect } from 'react';

const Main = ( {navOpen} ) => {
    const MyChip = withStyles({
        root: {
            backgroundColor:'rgba(255, 255, 255, 0.1)',
            color: 'white',
            border: '1px solid #5c5c5c7c;',
            fontSize: '14px',
            cursor: 'pointer',
            margin: '0px 5px 0px 5px',
            '&:hover': {
                backgroundColor: 'rgba(255, 255, 255, 0.2)',
                }
            }     
    })(Chip);

    const videoArr = [
        {
            thumbnail: '../images/thumbnail1.jpg',
            length: '4:08',
            title: 'J-Cole - False Prophets',
            avatar: 'images/avatar1.jpg',
            channel: 'Dreamville',
            views: 38 + 'M Views',
            date: 4 + ' years ago',
            embedSrc: 'https://www.youtube.com/embed/wvFDXV0VBg4?playlist=wvFDXV0VBg4&autoplay=1&mute=1&controls=0&loop=1',
            videoSrc: 'https://www.youtube.com/watch?v=wvFDXV0VBg4',
            topics: ['J Cole', 'Dreamville', 'Simba', '2014 FHD']
        },
        {
            thumbnail: '../images/thumbnail2.jpg',
            length: '14:41',
            title: 'Samsung Galaxy S21 Ultra Review: Problems Solved!',
            avatar: 'images/avatar2.jpg',
            channel: 'Marques Brownlee',
            views: 256 + 'K Views',
            date: 1 + ' hour ago',
            embedSrc: 'https://www.youtube.com/embed/tn2AgrwXpNQ?playlist=tn2AgrwXpNQ&autoplay=1&mute=1&controls=0&loop=1',
            videoSrc: 'https://www.youtube.com/watch?v=tn2AgrwXpNQ',
            topics: ['MKBHD', 'Samsung', 'Technology', 'Smartphone']
        },
        {
            thumbnail: '../images/thumbnail3.jpg',
            length: '0:41',
            title: '6 Stroke Single Grid | BYOS | Jeremie',
            avatar: 'images/avatar3.jpg',
            channel: 'Jeremie Foster',
            views: 531 + ' Views',
            date: 6 + ' days ago',
            embedSrc: 'https://www.youtube.com/embed/MYsk1xceuGg?playlist=MYsk1xceuGg&autoplay=1&mute=1&controls=0&loop=1',
            videoSrc: 'https://www.youtube.com/watch?v=MYsk1xceuGg',
            topics: ['HBCU', 'Drums', 'Grids', 'Snare Drum']
        },
        {
            thumbnail: '../images/thumbnail4.jpg',
            length: '6:55',
            title: 'First Take gets heated debating LeBron vs. Kevin Durant',
            avatar: 'images/avatar4.jpg',
            channel: 'ESPN',
            views: 1 + 'M Views',
            date: 3 + ' months ago',
            embedSrc: 'https://www.youtube.com/embed/VgMcpuKntxA?playlist=VgMcpuKntxA&autoplay=1&mute=1&controls=0&loop=1',
            videoSrc: 'https://www.youtube.com/watch?v=VgMcpuKntxA&t=5s',
            topics: ['Basketball', 'Lakers', 'NBA', 'LeBron']
        },
        {
            thumbnail: '../images/thumbnail5.jpg',
            length: '1:34:56',
            title: '🔥🔥🔥The Boston Roast Session Late Show w/ DC Young Fly, Karlous Miller and Chico Bean',
            avatar: 'images/avatar5.jpg',
            channel: 'The 85 South Comedy Show',
            views: 947 + 'K Views',
            date: 1 + ' week ago',
            embedSrc: 'https://www.youtube.com/embed/S9xH4qIE5D4?playlist=S9xH4qIE5D4&autoplay=1&mute=1&controls=0&loop=1',
            videoSrc: 'https://www.youtube.com/watch?v=S9xH4qIE5D4',
            topics: ['Chico Bean', 'Atlanta', 'DC Young Fly', '85 South Show']
        },
        {
            thumbnail: '../images/thumbnail6.jpg',
            length: '2:49',
            title: 'The AMAZING SPIDER-MAN 2 - Official Trailer #2 (HD)',
            avatar: 'images/avatar6.jpg',
            channel: 'Sony Pictures Entertainment',
            views: 16 + 'M Views',
            date: 6 + ' years ago',
            embedSrc: 'https://www.youtube.com/embed/DlM2CWNTQ84?playlist=DlM2CWNTQ84&autoplay=1&mute=1&controls=0&loop=1',
            videoSrc: 'https://www.youtube.com/watch?v=DlM2CWNTQ84&t=82s',
            topics: ['DC', 'Avengers', 'Marvel', 'Spider Man']
        },
        {
            thumbnail: '../images/thumbnail7.jpg',
            length: '5:25',
            title: 'Ari Lennox - Whipped Cream',
            avatar: 'images/avatar7.jpg',
            channel: 'Ari Lennox',
            views: 13 + 'M Views',
            date: 2 + ' years ago',
            embedSrc: 'https://www.youtube.com/embed/zM0lu8xx0R4?playlist=zM0lu8xx0R4&autoplay=1&mute=1&controls=0&loop=1',
            videoSrc: 'https://www.youtube.com/watch?v=zM0lu8xx0R4',
            topics: ['SZA', 'R&B', 'Music', 'Ari Lennox']
        },
        {
            thumbnail: '../images/thumbnail8.jpg',
            length: '13:35',
            title: "NCAT's 'Cold Steel' Percussion Feature @ the 2019 Band Brawl",
            avatar: 'images/avatar8.jpg',
            channel: 'Killa Kev Productions',
            views: 72 + 'K Views',
            date: 1 + ' year ago',
            embedSrc: 'https://www.youtube.com/embed/uAO00n6KGPs?playlist=uAO00n6KGPs&autoplay=1&mute=1&controls=0&loop=1',
            videoSrc: 'https://www.youtube.com/watch?v=uAO00n6KGPs&t=427s',
            topics: ['Drumline', 'BGMM', 'Marching Band', 'Coldsteel']
        }
    ]
    
    const videos = [];
    const topicArr = [];

    for (let i = 0; i < videoArr.length; i++) {
        videos.push(videoArr[i])
    }
    for (let i = 0; i < videoArr.length; i++) {
        for (let j = 0; j < videoArr[i].topics.length; j++) {
            topicArr.push(videoArr[i].topics[j]);
        }
    }
    for (let i = topicArr.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * i);
        const temp = topicArr[i];
        topicArr[i] = topicArr[j];
        topicArr[j] = temp;
    }
    for (let i = videos.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * i);
            const temp = videos[i];
            videos[i] = videos[j];
            videos[j] = temp;
    }

    return (
        <div className={`${navOpen ? "main-container-cd" : "main-container"}`}>
            <div className={`${navOpen ? "chip-bar-container-cd" : "chip-bar-container"}`}>
                <Tabs className="chip-bar" variant="scrollable" scrollButtons="on">
                    {topicArr.map(topic => (
                    <MyChip 
                        label={topic}
                    />
                ))}
                </Tabs>
            </div>
            <div className="video-grid-container">
                    {videos.map(video => (
                    <Video 
                        thumbnail={video.thumbnail}
                        length={video.length}
                        title={video.title}
                        avatar={video.avatar}
                        channel={video.channel}
                        views={video.views}
                        date={video.date}
                        embedSrc={video.embedSrc}
                        videoSrc={video.videoSrc}
                    />
                ))}
            </div>
        </div>
    )
}

export default Main;
