import React from 'react';
import './Video.css';
import Avatar from '@material-ui/core/Avatar';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import WatchLaterIcon from '@material-ui/icons/WatchLater';
import { useState } from 'react';
import FeedbackIcon from '@material-ui/icons/Feedback';
import FlagIcon from '@material-ui/icons/Flag';
import NotInterestedIcon from '@material-ui/icons/NotInterested';
import RemoveCircleOutlineIcon from '@material-ui/icons/RemoveCircleOutline';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';


const ChannelTooltip = withStyles(() => ({
    tooltip: {
        fontSize: 12,
        padding: 9
    },
}))(Tooltip);

const CustomTooltip = withStyles(() => ({
    tooltip: {
        fontSize: 12,
        padding: 9,
        backgroundColor: 'rgba(0,0,0,0.8)',
        marginRight: '1px',
        borderRadius: 0,
        height: '12px',
        display: 'flex',
        alignItems: 'center'
    },
}))(Tooltip);

const Video = ( {thumbnail, length, title, avatar, channel, views, date, embedSrc, videoSrc} ) => {

    const [hover, setHover] = useState(false);
    const [videoMenu, setVideoMenu] = useState(false);

    const handleHover = () => {
        if (hover === false) {
            setHover(true);
        } else {
            setHover(false);
        }
    }
    const toggleVideoMenu = () => {
        if (videoMenu === true) {
            setVideoMenu(false);
        } else {
            setVideoMenu(true);
        }
    }
    const handleClickAway = () => {
        setVideoMenu(false);
    }

    return (
        <div className="video-container" onMouseEnter={handleHover} onMouseLeave={handleHover}>
            <a>
                <div className="thumbnail-container">
                    <img src={thumbnail}/>
                    <div className={`${hover ? "hover-btn-container" : "hidden"}`}>
                        <CustomTooltip title='WATCH LATER' placement="left" enterDelay={600} leaveDelay={0}>
                            <span className="hover-btns"><WatchLaterIcon/></span>
                        </CustomTooltip>
                        <CustomTooltip title='ADD TO QUEUE' placement="left" enterDelay={600} leaveDelay={0}>
                            <span className="hover-btns"><span class="iconify" data-icon="mdi:menu-open" data-inline="false"></span></span>
                        </CustomTooltip>
                    </div>
                    <iframe 
                        className={`${hover ? "iframe" : "iframe-hidden"}`} src={embedSrc} frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen>
                    </iframe>
                    <div className={`${hover ? "hidden" : "timestamp-container"}`}>
                        <span className="timestamp">{length}</span>
                    </div>
                </div>
                <div className="details-container">
                    <span className="channel-thumbnail">
                        <Avatar alt="Profile Imange" src={avatar}/>
                    </span>
                    <div className="video-details">
                            <h1 className="video-title"><a href={videoSrc}>{title}</a></h1>
                            <ChannelTooltip title={channel} placement="top-start"><h4 className="channel">{channel}</h4></ChannelTooltip>
                            <h4>{views} • {date}</h4>
                    </div>
                    <ClickAwayListener onClickAway={handleClickAway}>
                        <div className="video-menu-btn" onClick={toggleVideoMenu}>
                            <MoreVertIcon className="menu-btn"></MoreVertIcon>
                            <ul className={`${videoMenu ? "video-options" : "hidden"}`}>
                                <span className="top-divider"></span>
                                <div className="video-nav-item">
                                    <span className="video-nav-icon"><span class="iconify" data-icon="mdi:menu-open" data-inline="false"></  span></span>
                                    <li>Add to queue</li>
                                </div>
                                <div className="video-nav-item">
                                    <WatchLaterIcon className="video-nav-icon"/>
                                    <li>Save to Watch later</li>
                                </div>
                                <div className="video-nav-item">
                                    <span className="iconify video-nav-icon" data-icon="mdi:playlist-plus" data-inline="false"></span>
                                    <li>Save to playlist</li>
                                </div>
                                <span className="divider"></span>
                                <div className="video-nav-item">
                                    <NotInterestedIcon className="video-nav-icon"/>
                                    <li>Not interested</li>
                                </div>
                                <div className="video-nav-item">
                                    <RemoveCircleOutlineIcon className="video-nav-icon"/>
                                    <li>Don't recommend channel</li>
                                </div>
                                <div className="video-nav-item">
                                    <FlagIcon className="video-nav-icon"/>
                                    <li>Report</li>
                                </div>
                                <span className="top-divider"></span>
                            </ul>
                    </div>
                    </ClickAwayListener>
                    
                </div>
            </a>
        </div>
    )
}

export default Video;